import * as React from 'react';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Unstable_Grid2';

import { ImgIcon } from './Img';
import { TextRs } from './Text';

const ElList = ['炎属性', '水属性', '氷属性', '風属性', '岩属性', '雷属性', '冥属性', '無属性']
const RsList = [
  ['炎属性', '水属性', '氷属性', '風属性'],
  ['岩属性', '雷属性', '冥属性'],
  ['睡眠', '毒', '暗闇', '呪詛', '麻痺', '混乱', '魅了'],
  ['物理封印', '体技封印', '波動封印', '魔法封印', '移動封印', '行動封印'],
]

const isIncludes = (arr, target) => {
  // console.log(arr)
  // console.log(target)
  return arr.some(el => target.includes(el))
}// 複数の特定要素のうちひとつでも当てはまったら true を返す





export function GridRs(props) {
  const { data, cNameF, AE } = props.props
  return (
    <Grid container spacing={0} sx={{ fontSize: 15 }}>
      属性耐性
      <Grid xs={12}>
        <Grid container spacing={0} sx={{ bgcolor: '#EDE2D5' }} columns={14} >
          {[0, 1].map((n, i) => {
            return (
              <React.Fragment key={i}>
                <Grid xs={1} textAlign='right' >
                  {RsList[n].map((name, i) => { return <React.Fragment key={i}><ImgIcon name={name.split('属性')[0]} height={17} /><br /></React.Fragment> })}
                </Grid>
                <Grid xs={2} textAlign='center'>
                  {RsList[n].map((name, i) => { return <React.Fragment key={i}>{name.split('属性')[0]}<br /></React.Fragment> })}
                </Grid>
                <Grid xs={1} textAlign='center'>
                  {RsList[n].map((name, i) => { return <React.Fragment key={i}><TextRs V={data.C1[cNameF][AE].耐性[name]} /><br /></React.Fragment> })}
                </Grid>
                <Grid xs={1} textAlign='right'>
                  {RsList[n].map((name, i) => { return <React.Fragment key={i}>{data.C1[cNameF][AE].耐性[name] + '%'}<br /></React.Fragment> })}
                </Grid>
                <Grid xs={1}>
                </Grid>
              </React.Fragment>
            )
          })}
        </Grid>
      </Grid>
      異常耐性
      <Grid xs={12}>
        <Grid container spacing={0} sx={{ bgcolor: '#EDE2D5' }} columns={14}>
          {[2, 3].map((n, i) => {
            return (
              <React.Fragment key={i}>
                <Grid xs={1} textAlign='right'>
                  {RsList[n].map((name, i) => { return <React.Fragment key={i}><ImgIcon name={name.split('属性')[0]} height={17} /><br /></React.Fragment> })}
                </Grid>
                <Grid xs={2} textAlign='center'>
                  {RsList[n].map((name, i) => { return <React.Fragment key={i}>{name.split('属性')[0]}<br /></React.Fragment> })}
                </Grid>
                <Grid xs={1} textAlign='center'>
                  {RsList[n].map((name, i) => { return <React.Fragment key={i}><TextRs V={data.C1[cNameF][AE].耐性[name]} /><br /></React.Fragment> })}
                </Grid>
                <Grid xs={1} textAlign='right'>
                  {/* {RsList[n].map((name, i) => { return <React.Fragment key={i}>{data.C1[cNameF][AE].耐性[name] + '%'}<br /></React.Fragment> })} */}
                </Grid>
                <Grid xs={1}>
                </Grid>
              </React.Fragment>
            )
          })}
        </Grid>
      </Grid>
    </Grid>
  );
}
export function GridAmp(props) {
  const { data, cNameF, AE } = props.props
  return (
    <Grid container spacing={0} textAlign='right' sx={{ fontSize: 14 }}>
      <Grid xs='auto'>
        <br />
        <br />
        {ElList.map((name, i) => { return <React.Fragment key={i}><ImgIcon name={name.split('属性')[0]} height={17} />{name}<br /></React.Fragment> })}
      </Grid>
      <Grid xs={1}>
        <br />
        {data.C1[cNameF][AE].威力['全'] + '%'}<br />
        {ElList.map((name, i) => { return <React.Fragment key={i}>{data.C1[cNameF][AE].威力[name] + '%'}<br /></React.Fragment> })}
      </Grid>
      <Grid xs={1}>
        物理<br />
        {data.C1[cNameF][AE].威力['物理'] + '%'}<br />
        {ElList.map((name, i) => { return <React.Fragment key={i}>{data.C1[cNameF][AE].威力[name + '物理'] + '%'}<br /></React.Fragment> })}
      </Grid>
      <Grid xs={1}>
        体技<br />
        {data.C1[cNameF][AE].威力['体技'] + '%'}<br />
        {ElList.map((name, i) => { return <React.Fragment key={i}>{data.C1[cNameF][AE].威力[name + '体技'] + '%'}<br /></React.Fragment> })}
      </Grid>
      <Grid xs={1}>
        波動<br />
        {data.C1[cNameF][AE].威力['波動'] + '%'}<br />
        {ElList.map((name, i) => { return <React.Fragment key={i}>{data.C1[cNameF][AE].威力[name + '波動'] + '%'}<br /></React.Fragment> })}
      </Grid>
      <Grid xs={1}>
        魔法<br />
        {data.C1[cNameF][AE].威力['魔法'] + '%'}<br />
        {ElList.map((name, i) => { return <React.Fragment key={i}>{data.C1[cNameF][AE].威力[name + '魔法'] + '%'}<br /></React.Fragment> })}
      </Grid>
      <Grid xs={1}>
      </Grid>
      <Grid xs='auto'>
        {data.C1[cNameF][AE].Skl.filter(Nam => isIncludes(Object.keys(data.C1[cNameF][AE].威力), Nam)).map((Nam, i) => {//filterで威力+があるスキルが配列で返ってくるのでそれをmap
          return <React.Fragment key={i}>{Nam + '威力+' + data.C1[cNameF][AE].威力[Nam] + '%'}<br /></React.Fragment>
        })}
      </Grid>

    </Grid>
  );
}

const ElList2 = ['', '炎属性', '水属性', '氷属性', '風属性', '岩属性', '雷属性', '冥属性', '無属性']
const sho = ['(大)','', '(小)']
const BTHM = ['', '物理', '体技', '波動', '魔法']

export function GridAdv(props) {
  const { data, cNameF, AE, Ctg } = props.props
  return (
    <Grid container spacing={0} textAlign='right' sx={{ fontSize: 14 }} columns={32}>
      <Grid textAlign='left' xs={2}>
        [%]
      </Grid>
      <Grid textAlign='center' xs={10} sx={{ bgcolor: '#CCC' }}>
        {Ctg+ '(大)'}
      </Grid>
      <Grid textAlign='center' xs={10} sx={{ bgcolor: '#DDD' }}>
        {Ctg}
      </Grid>
      <Grid textAlign='center' xs={10} sx={{ bgcolor: '#EEE' }}>
        {Ctg + '(小)'}
      </Grid>
      <Grid xs={2} textAlign='left'>
        <br />
        {ElList2.map((name, i) => {
          return (
            <React.Fragment key={i}>
              <ImgIcon name={name.split('属性')[0]} height={17} />
              {name.split('属性')[0]}
              <br />
            </React.Fragment>
          )
        })}
      </Grid>
      {sho.map((sh, i) => {
        return (
          <React.Fragment key={i}>
            {BTHM.map((BTHM, i) => {
              return (
                <Grid xs={2} key={i}>
                  {BTHM}<br />
                  {ElList2.map((name, i) => {
                    return (
                      <React.Fragment key={i}>
                        {data.C1[cNameF][AE][Ctg][name + BTHM + Ctg + sh]}
                        <br />
                      </React.Fragment>
                    )
                  })}
                </Grid>
              )
            })}
          </React.Fragment>
        )

      })

      }
    </Grid>
  );
}




export function GridCut(props) {
  const { data, cNameF, AE } = props.props
  return (
    <Grid container spacing={0} textAlign='right'>
      <Grid xs='auto'>

        {data.list.増減.map((name, i) => { return <React.Fragment key={i}>{name}<br /></React.Fragment> })}
      </Grid>
      <Grid xs={1}>
        {data.list.増減.map((name, i) => { return <React.Fragment key={i}>{data.C1[cNameF][AE].増減[name] + '%'}<br /></React.Fragment> })}
      </Grid>

    </Grid>
  );
}



