import * as React from 'react';
import Box from '@mui/material/Box';
import { DataGrid } from '@mui/x-data-grid';
import Tooltip from '@mui/material/Tooltip';
import InfoIcon from '@mui/icons-material/Info';

import { ImgIcon, ImgDmg } from './Img';
import StyleCommon from './StyleCommon';

export default function DataGridDmgChr(props) {
  const { data, cNameF, AE } = props.props

  const columns = [
    {
      field: 'No',
      headerName: '',
      width: 50,
      disableColumnMenu: true,//メニュー不可
      align: 'center',
    },
    {
      field: 'Nam',
      headerName: '',
      width: 162,//アブソリュート・ゼロ
      disableColumnMenu: true,//メニュー不可
    },
    {
      field: 'Ara',
      headerName: '',
      headerAlign: 'center',align: 'center',
      width: 70,
      disableColumnMenu: true,//メニュー不可
      renderCell: (params) => (
        <ImgIcon name={params.row.Ara} width={70} />//height={35}
      )
    },
    {
      field: 'Dmg',
      headerName: 'ダメージ',
      headerAlign: 'center',align: 'right',
      width: 120,
      disableColumnMenu: true,//メニュー不可
      renderHeader: (params) => (
        <>
          <Tooltip title="攻撃直前にかかるバフも反映されます。" placement="top-start" arrow >
            <InfoIcon color="disabled" />
          </Tooltip>
          ダメージ
        </>
      ),
      renderCell: (params) => params.row.DmgTxt,
    },
    {
      field: 'DmgBar',
      headerName: '',
      width: 100,
      disableColumnMenu: true,//メニュー不可
      renderCell: (params) => (
        <ImgDmg
          data={data}
          Dmg={params.row.Dmg}
          HP={AE === 'Ally' ? data.Enmy[data.SlEnmy].St.Bas.HP : params.row.ZHP}
          AE={AE}
        />
      )
    },
    {
      field: 'BasDmgStr',
      headerName: '基礎ダメージ&強化(+10)',
      width: 248,//燃え燃えサンタ
      align: 'left',
      disableColumnMenu: true,//メニュー不可
    },
    {
      field: 'Amp',
      headerName: '威力',
      width: 50,
      align: 'center',
      disableColumnMenu: true,//メニュー不可
    },
    {
      field: '逆境盤石',
      headerName: '逆・盤',
      headerAlign: 'center',
      width: 64,//1.1111
      align: 'center',
      disableColumnMenu: true,//メニュー不可
    },
    {
      field: '耐性',
      headerName: '耐性',
      width: 50,
      align: 'center',
      disableColumnMenu: true,//メニュー不可
    },
    {
      field: '増減',
      headerName: '増減',
      width: 50,
      align: 'center',
      disableColumnMenu: true,//メニュー不可
    },
  ];

  const rows = data.C1[cNameF][AE].Skl3.map((SklEl, i) => {
    return({
      ...SklEl,
      id: i,
      Elm: data.Skl[SklEl.Nam] ? data.Skl[SklEl.Nam].Elm : data.Ab[SklEl.Nam].Elm,
      DmgBar: SklEl.Dmg,
      ZHP: data.C1[cNameF].Ally.St.Btl.HP,
    })
  })

  return (
    <Box sx={{ height: 250, width: '100%' }}>
      <DataGrid
        rows={rows}
        columns={columns}
        experimentalFeatures={{ newEditingApi: true }}
        density='compact'//行の幅
        // autoHeight//行に合わせた高さ
        hideFooter//フッター非表示
        hideFooterSelectedRowCount//フッターに表示されている選択中の行数を非表示
        hideFooterPagination//フッターに表示されているページ当たりの行数、ページングを非表示にする。(無料版のみ）

        sx={StyleCommon}
        getRowClassName={(params) => params.row.Elm}


      // initialState={{
      //   pagination: {
      //     paginationModel: {
      //       pageSize: 5,
      //     },
      //   },
      // }}
      // pageSizeOptions={[5]}
      // checkboxSelection
      // disableRowSelectionOnClick
      // クラスを設定しておく

      />
    </Box>
  );
}