import * as React from 'react';

import TrmToF from "./TrmToF"

const BtTgHdMh = ['物理', '体技', '波動', '魔法']
const ElList = ['炎属性', '水属性', '氷属性', '風属性', '岩属性', '雷属性', '冥属性', '無属性']
const GokuiKnd = ['威力+', '回復効果+', '消費TP-']

const EA = { 'Ally': 'Enmy', 'Enmy': 'Ally' }






// const EffAdd = (props) => {
export default function EffAdd(props) {
  const { data, cNameF, AbName, isIncludes, AE } = props//ZHPR

  // let V = ''
  // if (AbName.includes('+')) {
  //   V = Number(AbName.split('+')[1].split('%')[0]) //効果量
  // }

  // if(cNameF==='【ナース姿の先生】アリスカ'){
  //   console.log(AbName)
  // }

  // if (cNameF === '【砂漠に従いし怠惰の使徒】ナディラ(鏡界)') {
  //   console.log(AbName)
  // }



  let V = AbName.includes('+') ? Number(AbName.split('+')[1].split('%')[0]) : ''//AbNamに「+」があるなら先にVに入れておく

  // console.log(cNameF,AbName,AE)


  if (AbName.includes('移動力+')) {
    if (AE === 'Ally') data.C1[cNameF][AE].St.Bas.移動力 += 1//味方ならBasにも
    data.C1[cNameF][AE].St.Org.移動力 += V

  } else if (AbName.includes('属性耐性+')) {//覚醒時のものなのでバフではない
    let Elm = AbName.split('耐性+')[0]
    data.C1[cNameF][AE].耐性[Elm] += V

  } else if (AbName.includes('威力+')) {

    data.C1[cNameF][AE].Eff0.push({
      N: '特性(パッシブ,アビリティ)',
      E: AbName.split('+')[0] + '+',
      V: AbName.split('+')[1],
    })

  } else if (
    AbName.includes('の極意') &&
    (isIncludes(BtTgHdMh, AbName) || isIncludes(ElList, AbName))//固有の極意を除くため
  ) {

    GokuiKnd.forEach((Knd) => {
      data.C1[cNameF][AE].Eff0.push({
        N: AbName,
        E: AbName.split('の極意')[0] + Knd,
        V: AbName.includes('・極') ? '20%' : '10%',
      })
    })

  } else if (AbName.includes('消費TP')) {
  } else if (AbName.includes('効果+')) {

  } else {

    // if (cNameF === '【砂漠に従いし怠惰の使徒】ナディラ(鏡界)') {
    // console.log(AbName)
    // }


    if (data.Ab[AbName].Eff) {//効果ありのアビなら



      data.Ab[AbName].Eff.forEach((EffEl, i) => {

        if (EffEl.Stk) {//Stkがあるなら(Eff0共通)
          let AE2 = AE
          if (EffEl.Tgt === 'Enmy') {
            AE2 = EA[AE]
          }

          data.C1[cNameF][AE2].Eff0.push({
            ...EffEl,
            N: AbName,
          })

          if (EffEl.All) {
            let V = EffEl.V ? EffEl.V : ''
            data.OthEff[AbName + '_' + EffEl.E + V] = EffEl.Stk
          }
        } else {
          if (EffEl.All) {
            if (data.OthEff[AbName + '_' + EffEl.E + V]) {
              let V = EffEl.V ? EffEl.V : ''
              delete data.OthEff[AbName + '_' + EffEl.E + V]
            }
          }
        }

      })



      data.C1[cNameF][AE].Ab2.push({//スキルリストに表示する用
        ...data.Ab[AbName],
        Nam: AbName,
        No: '特性',
      })

    }

  }
}

