export default function CalcData(data, isIncludes) {
  // const { data } = props




  Object.keys(data.Bddy).forEach((BddyName) => {//BddyのOpリスト作成
    let UqOp = data.Bddy[BddyName].UqOp
    if (data.BddyOp[UqOp]) {//=UqOpがに上位互換がなければ
      if (!data.BddyOp[UqOp].Bddy) {//枠が無ければ
        data.BddyOp[UqOp].Bddy = []
      }
      data.BddyOp[UqOp].Bddy.push(BddyName)
    }
  })

  data.ESklAb = {}//Effがあるスキルとアビをまとめる
  data.list.SklAb.forEach((SA) => {
    Object.keys(data[SA]).forEach((name) => {
      if (data[SA][name].Eff) {
        data.ESklAb[name] = data[SA][name]
      }
    })
  })







  Object.keys(data.Chr).forEach((cName1, i) => {//Skl0(ミューテーションは配列)からSkl1(全て並列)を作成
    Object.keys(data.Chr[cName1]).forEach((cName2) => {
      data.Chr[cName1][cName2].Skl1 = []
      if (data.Chr[cName1][cName2].Skl0) {
        data.Chr[cName1][cName2].Skl0.forEach((SklNam) => {
          if (Array.isArray(SklNam)) {
            data.Chr[cName1][cName2].Skl1.push(...SklNam)
          } else {
            data.Chr[cName1][cName2].Skl1.push(SklNam)
          }
        })
      }
    })
  })

  data.C1 = {}
  let Enmy = data.SlEnmy
  Object.keys(data.Chr).forEach((cName1) => {
    Object.keys(data.Chr[cName1]).forEach((cName2) => {

      console.log(data.Chr[cName1][cName2].memo)

      let cNameF = '【' + cName2 + '】' + cName1

      if (data.Chr[cName1][cName2].Rr === 'S') {
        data.C1[cNameF] = {//_Calc毎に初期化が不要なもの
          Ally: {
            Gr: data.Chr[cName1][cName2].Gr,
            Awk: 5,
            Lv: data.Lv[data.Chr[cName1][cName2].Rr] + data.EXLv,
            St: { Bas: {}, Org: {}, Btl: {} },
            Wepn: data.Chr[cName1][cName2].Wepn,
            Armr: data.Chr[cName1][cName2].Armr,
            Bddy: data.Chr[cName1][cName2].Bddy,
            Srs: {},
            Skl0: data.Chr[cName1][cName2].Skl0,//ミューテーションは配列
            Skl1: data.Chr[cName1][cName2].Skl1,//全て並列
            // Skl: data.Chr[cName1][cName2].Skl,
            // Skl2: [
            //   { ...data.Skl[data.Chr[cName1][cName2].Skl[0]], No: 'S1', Nam: data.Chr[cName1][cName2].Skl[0], Str: 50 },
            //   { ...data.Skl[data.Chr[cName1][cName2].Skl[1]], No: 'S2', Nam: data.Chr[cName1][cName2].Skl[1], Str: 50 },
            //   { ...data.Skl[data.Chr[cName1][cName2].Skl[2]], No: 'S3', Nam: data.Chr[cName1][cName2].Skl[2], Str: 50 },
            // ],
            Skl3: [],
            Ab: data.Chr[cName1][cName2].Ab,
            UqP: [],
            ZHP: { V: 1, R: 100 },
          },
          Enmy: {
            St: {
              Bas: {},
              Org: {
                'HP': 0, 'TP': 0, '攻撃力': 0, '防御力': 0, '速力': 0, '精神力': 0, '移動力': 0
              },
              Btl: {}
            },
            Lv: data.Lv[data.Enmy[Enmy].Rr],
            Skl0: data.Chr[cName1][cName2].Skl0,//ミューテーションは配列

            // Skl: data.Enmy[Enmy].Skl,
            Skl3: [],
            Ab: data.Enmy[Enmy].Ab,
            UqP: [],
            ZHP: { V: 1, R: 100 }
          }
        }


        data.Chr[cName1][cName2].Skl1.forEach((SklNam) => {//自分のスキルforEach
          let SklNum = data.Chr[cName1][cName2].Skl0.indexOf(SklNam) + 1//変化しないスキルならSkl0のindex+1を表示
          if (SklNum < 1) {//変化するスキルなら
            data.Chr[cName1][cName2].Skl0.forEach((Skl0, i) => {//Skl0をforEach
              if (Array.isArray(Skl0) && isIncludes(Skl0, SklNam)) {//そのSkl0が配列で、スキルが含まれているなら
                SklNum = i + 1//まずSkl0のindex+1
                SklNum += Skl0.indexOf(SklNam) === 0 ? 'N' : 'M'//その配列の0ならNormal、1ならMutation
              }
            })
          }
          data.C1[cNameF].Ally.Skl3.push(
            { ...data.Skl[SklNam], Nam: SklNam, No: SklNum, Str: 50 }
          )
        })




        Object.keys(data.Chr[cName1]).forEach((cName22) => {//同名キャラforEach//継承スキルをSkl3にセット
          let Rr = data.Chr[cName1][cName22].Rr//同名キャラのレアリティ

          data.Chr[cName1][cName22].Skl1.forEach((SklNam, i) => {//同名キャラのスキルforEach
            // console.log(data.C1[cNameF][cName2])
            let Mt = false

            let SklNum
            if (isIncludes(data.Chr[cName1][cName22].Skl0, SklNam)) {//Mutation無しのスキルなら
              SklNum = data.Chr[cName1][cName22].Skl0.indexOf(SklNam)
            } else {//Mutation有りのスキルなら
              data.Chr[cName1][cName22].Skl0.forEach((Skl0, i) => {
                if (Array.isArray(Skl0)) {
                  if (isIncludes(Skl0, SklNam)) {
                    SklNum = i
                    if (Skl0.indexOf(SklNam) === 1) {
                      Mt = true
                    }
                  }
                }
              })
            }

            // console.log(SklNum, SklNam, Mt)

            if (
              (cName2 !== cName22) &&//バージョン違い
              (Rr !== 'S' || (Rr === 'S' && SklNum !== 2)) &&//レアSでない||レアSならスキル1・2のみ
              (!Mt)//Mutationでない
            ) {
              data.C1[cNameF].Ally.Skl3.push(
                { ...data.Skl[SklNam], No: '継承', Nam: SklNam, Str: 50 }
              )
            }
          })//同名キャラのスキルforEach



          // data.Chr[cName1][cName22].Skl.forEach((SklName, i) => {//同名キャラのスキルforEach
          //   // console.log(data.C1[cNameF][cName2])
          //   if (
          //     (cName2 !== cName22) &&
          //     (data.Chr[cName1][cName22].Rr !== 'S' || (data.Chr[cName1][cName22].Rr === 'S' && i !== 2))
          //   ) {
          //     data.C1[cNameF].Ally.Skl3.push(
          //       { ...data.Skl[SklName], No: '継承', Nam: SklName, Str: 50 }
          //     )
          //   }
          // })//同名キャラのスキルforEach

          data.Chr[cName1][cName22].UqP.forEach((AbNam) => {//同名キャラ固有パッシブを入れる
            data.C1[cNameF].Ally.UqP.push(AbNam)

            if (data.Ab[AbNam] && data.Ab[AbNam].Mag) {//Magが設定されているならSkl3にも入れる
              data.C1[cNameF].Ally.Skl3.push(
                { ...data.Ab[AbNam], No: '特性', Nam: AbNam }//, Str: 50 
              )
            }
          })

        })//同名キャラforEach//継承スキルをSkl3にセット





        data.Chr[cName1][cName2].ExP.forEach((name, i) => {
          if (data.Ab[name] && data.Ab[name].Mag) {//Magが設定されているならSkl3にも入れる
            data.C1[cNameF].Ally.Skl3.push(
              { ...data.Ab[name], No: '特性', Nam: name }//, Str: 50 
            )
          }
        })
        Object.keys(data.Chr[cName1][cName2].Ab).forEach((name, i) => {//アビforEach
          // if(cNameF==='【一番いいのを頼むわ】ソフィア'){
          //   console.log(name)
          // }
          if (data.Ab[name] && data.Ab[name].Mag) {//Magが設定されているならSkl3にも入れる
            data.C1[cNameF].Ally.Skl3.push(
              { ...data.Ab[name], No: '特性', Nam: name }//, Str: 50 
            )
          }
        })
        data.C1[cNameF].Ally.Skl = data.C1[cNameF].Ally.Skl3.map((SklEl) => {//Skl3からSkl(配列)作成
          return SklEl.Nam
        })





        data.Enmy[Enmy].Skl.forEach((SklName, i) => {
          data.C1[cNameF].Enmy.Skl3.push(
            { ...data.Skl[SklName], No: 'S' + (i + 1), Nam: SklName, Str: 50, Num: 1 }
          )
        })







      }
    })
  })

  // console.log(data.C1['【一番いいのを頼むわ】ソフィア'].Ally.Skl3)
  // console.log(data.C1['【大樹に従いし慈悲の熾天聖】ソフィア'].Ally.Skl3)



}