import * as React from 'react';

import Data from "./Data";
import Tabtop from "./TabTop";
import CalcSt from "./CalcSt"
import EffAdd from "./EffAdd"
import TrmToF from "./TrmToF"
import MM from "./MM"
import CNSplit from "./CNSplit"
import CalcData from "./CalcData"

const roundAdv = (V) => {//小数2桁まで
  return (Math.round((V - 1) * 10000)) / 100
}

const EVToV = (EV) => {
  return EV.includes('+') ? EV.split('+')[1] : EV.includes('-') ? EV.split('-')[1] : ''
}

const isIncludes = (arr, target) => {
  // console.log(arr)
  // console.log(target)
  return arr.some(el => target.includes(el))
}// 複数の特定要素のうちひとつでも当てはまったら true を返す

const floor = (V) => {
  return (Math.floor(Math.round(V * 10000) / 10000))
}
const round = (V) => {//四捨五入で0.5は切捨て
  return (Math.ceil(V - 0.5))
}
const min0max100 = (V) => {
  return (Math.min(Math.max(V, 0), 100))
}

const data = Data()
const EA = data.EA
CalcData(data, isIncludes)
let TDmg = 0

export default function Calc() {

  const [Unit, setUnit] = React.useState(0)
  const hoge = (TDmg) => {
    setUnit(TDmg)
  }


  const Calc = () => {

    let t = data.t
    let Enmy = data.SlEnmy

    Object.keys(data.C1).forEach((cNameF, index) => {
      let cName1 = CNSplit(cNameF, 1)
      let cName2 = CNSplit(cNameF, 2)
      let Awk = data.C1[cNameF].Ally.Awk
      let ZHPRt = {}
      let Gr = data.Chr[cName1][cName2].Gr



      data.C1[cNameF].Ally.AbAll = [...data.C1[cNameF].Ally.UqP, ...data.Chr[cName1][cName2].ExP]//AbAll=特性全部(未習得含まず)
      Object.keys(data.Chr[cName1][cName2].Ab).forEach((AbName) => {
        if (TrmToF({ Trm: data.Chr[cName1][cName2].Ab[AbName], Awk: Awk })) { //アビ習得条件に合っているなら

          data.C1[cNameF].Ally.AbAll.push(AbName)
        }
      })

      data.C1[cNameF].Ally.AbAllNamed = []//AbAllNamed=特性のうちテキストがあるもの(DataGridChrSklで表示)
      data.C1[cNameF].Ally.AbAll.forEach((AbName) => {
        if (data.Ab[AbName]) {//&& data.Ab[AbName].Eff
          data.C1[cNameF].Ally.AbAllNamed.push(AbName)
        }
      })

      data.C1[cNameF].Ally.ESklAb = [...data.C1[cNameF].Ally.Skl, ...data.C1[cNameF].Ally.AbAllNamed]




      data.C1[cNameF].Enmy = {
        St: {
          Bas: {},
          Org: {
            'HP': 0, 'TP': 0, '攻撃力': 0, '防御力': 0, '速力': 0, '精神力': 0, '移動力': 0
          },
          Btl: {}
        },
        Lv: data.Lv[data.Enmy[Enmy].Rr],
        Skl: data.Enmy[Enmy].Skl,
        Skl3: [],

        Ab: data.Enmy[Enmy].Ab,
        AbAll: Object.keys(data.Enmy[Enmy].Ab),
        ZHP: { V: 1, R: 100 }
      }


      data.Enmy[Enmy].Skl.forEach((Nam, i) => {
        data.C1[cNameF].Enmy.Skl3.push(
          { ...data.Skl[Nam], No: 'S' + (i + 1), Nam: Nam, Str: 50, Num: 1 }
        )
      })





      let SklList


      data.list.AllyEnmy.forEach((AE) => {//初期化
        ZHPRt[AE] = data.C1[cNameF][AE].ZHP.R

        if (AE === 'Ally') {
          SklList = data.Chr[cName1][cName2].Skl
        } else {
          SklList = data.Enmy[Enmy].Skl
        }

        data.C1[cNameF][AE].Srs = {}//シリーズ効果リセット


        data.C1[cNameF][AE].Crr = 0
        data.C1[cNameF][AE].威力 = {}
        data.list.威力.forEach((name) => data.C1[cNameF][AE].威力[name] = 0)//威力リセット
        data.C1[cNameF][AE].耐性 = {}
        data.C1[cNameF][AE].増減 = {}
        data.list.増減.forEach((name) => data.C1[cNameF][AE].増減[name] = 0)//リセット

        // { 被ダメージ増加: 0, 被ダメージ軽減: 0}


        let GB = ['逆境', '盤石']
        GB.map((Knd) => {
          data.C1[cNameF][AE][Knd] = {}
          data.list[Knd].forEach((name) => data.C1[cNameF][AE][Knd][name] = 0)//逆境リセット
        })

        data.C1[cNameF][AE].回避 = false
        data.C1[cNameF][AE].与ダメージ回数 = {}
        data.C1[cNameF][AE].チャージ = {}
        data.C1[cNameF][AE].被ダメージ無効バリア = false

        data.C1[cNameF][AE].Ab2 = []
        data.C1[cNameF][AE].Eff = []
        data.C1[cNameF][AE].Eff0 = []
        data.C1[cNameF][AE].Eff1 = []
        data.C1[cNameF][AE].Eff9 = []

        data.C1[cNameF][AE].Eff2 = []
        // if (!data.C1[cNameF][AE].Eff2[t]) {
        data.C1[cNameF][AE].Eff2[t] = {}
        // }

        if (AE === 'Ally') {
          data.list.耐性.forEach((name) => {
            data.C1[cNameF].Ally.耐性[name] = data.Chr[cName1][cName2].Rs[name.split('属性')[0]] ? data.Chr[cName1][cName2].Rs[name.split('属性')[0]] : 0
          })
        } else {
          data.list.耐性.forEach((name) => {
            data.C1[cNameF].Enmy.耐性[name] = data.Enmy[Enmy].Rs[name.split('属性')[0]] ? data.Enmy[Enmy].Rs[name.split('属性')[0]] : 0
          })
        }


      })//data.list.AllyEnmy.forEachここまで(初期化




      data.list.AllyEnmy.forEach((AE) => {//ステータス計算

        if (AE === 'Ally') {
          CalcSt({
            data: data,
            cNameF: cNameF,
            isIncludes: isIncludes,
            AE: AE,
            round: round,
          })//自キャラの装備無/装備有ステータス
        } else {
          data.list.HTADSIM.forEach((S) => {//敵キャラの装備有ステータス
            data.C1[cNameF].Enmy.St.Org[S] = data.Enmy[Enmy].St.Bas[S]
          })
          data.C1[cNameF].Enmy.AbAll.forEach((AbName) => {
            EffAdd({
              data: data,
              cNameF: cNameF,
              AbName: AbName,
              isIncludes: isIncludes,
              AE: AE,
            })
          })

        }


        data.C1[cNameF][AE].Skl3.forEach((SklEl) => {//自スキルによるバフ・デバフ
          if (SklEl.Eff) {
            SklEl.Eff.map((EffEl) => {
              let AE2 = AE
              if (EffEl.Tgt === 'Enmy') {
                AE2 = EA[AE]
              }


              if (EffEl.Stk) {//Stkがあれば

                // if(EffEl.All){
                //   console.log(SklEl)
                // }

                if (//自分以外の効果は含めない
                  EffEl.OthOwn &&
                  data.C1[cNameF][AE].ESklAb.includes(SklEl.Nam)
                ) {
                } else {

                  data.C1[cNameF][AE2].Eff0.push({//Eff0にはバフ・デバフStk0は入れない
                    N: SklEl.Nam,
                    ...EffEl,
                  })


                }

                








                // if (EffEl.All) {//スキルで初期で有効は無いのでいらない
                //   data.OthEff[EffEl.Nam + '_' + EffEl.E + EffEl.V] = EffEl.Stk
                // }



              }
            })
          }
        })



        if (AE === 'Ally' && data.OthEffOn) {//他スキルによるバフ・デバフ
          Object.keys(data.OthEff).forEach((SklNEV) => {
            let N = SklNEV.split('_')[0]
            let EV = SklNEV.split('_')[1]
            let E = SklNEV.includes('+') ? SklNEV.split('_')[1].split('+')[0] + '+' :
              SklNEV.includes('-') ? SklNEV.split('_')[1].split('-')[0] + '-' :
                SklNEV.split('_')[1]



            if (!data.C1[cNameF][AE].ESklAb.includes(N)) {//自分のスキルでないなら
              if (
                !isIncludes(data.list.Gr, E) ||// 
                isIncludes(data.list.Gr, E) && E.includes(Gr)//グループ限定効果で合っているなら
              ) {
                data.ESklAb[N].Eff.map((EffEl) => {


                  // console.log(SklNEV)
                  // console.log(EV)
                  // console.log(EffEl.E + (EffEl.V ? EffEl.V : ''))


                  if (EV === (EffEl.E + (EffEl.V ? EffEl.V : ''))) {//他スキルのEVがdata.ESklAb[N].EffのEVと同じなら適用

                    let AE2 = AE
                    if (EffEl.Tgt === 'Enmy') {
                      AE2 = EA[AE]
                    }

                    if (
                      (!EffEl.Gr) ||//グループ限定でないなら
                      (EffEl.Gr && isIncludes(EffEl.Gr, Gr))//グループ限定で、該当するなら
                    ) {//Eff0にはバフ・デバフStk0は入れない
                      data.C1[cNameF][AE2].Eff0.push({ ...EffEl, N: N })
                    }
                  }
                })
              }
            }
          })
        }//他スキルによるバフ・デバフ
      })//ステータス計算


      if (data.LS !== 'なし') {//リーダースキルはEff9に直接入れる
        let AE = data.LS.includes('敵の') ? 'Enmy' : 'Ally'
        let EV = data.LS.includes('敵の') ? data.LS.split('敵の')[1] : data.LS.split('敵の')[0]
        data.C1[cNameF][AE].Eff9.push({
          N: 'リーダースキル',
          EV: EV,
          E: EV.includes('+') ? EV.split('+')[0] + '+' : EV.includes('-') ? EV.split('-')[0] + '-' : EV,//これ使う？
          V: EV.includes('+') ? EV.split('+')[1] : EV.includes('-') ? EV.split('-')[1] : '',//これ使う？
        })
      }

      data.list.AllyEnmy.forEach((AE) => {//バフ・デバフ整理
        // console.log(data.C1[cNameF][AE].Eff0)
        data.C1[cNameF][AE].Eff0.map((EffEl) => {
          let Eff1 = data.C1[cNameF][AE].Eff1
          let Stk = EffEl.Stk ? EffEl.Stk : 1//Eff1に入っている時点でStkあり

          if (!Eff1[EffEl.E]) {//EffEl.Eをキーにする
            Eff1[EffEl.E] = []
          }

          for (let n = 0; n < Stk; n++) {//Stk分まとめる
            Eff1[EffEl.E].push({ ...EffEl })
          }
        })

        Object.keys(data.C1[cNameF][AE].Eff1).map((EffName) => {//Eff1で重複削除
          let StkMax = 100

          if (
            isIncludes(data.HTADSI, EffName) ||//ステータスは最大3個
            EffName.includes('耐性')
          ) {
            StkMax = 3
          } else if (
            data.list.逆境.includes(EffName) ||
            data.list.盤石.includes(EffName) ||
            EffName === '回避'
          ) {
            StkMax = 1
          }



          if (data.C1[cNameF][AE].Eff1[EffName].length >= StkMax + 1) {
            data.C1[cNameF][AE].Eff1[EffName].length = StkMax
          }

        })

        if (data.C1[cNameF][AE].Eff1.中ドレイン && data.C1[cNameF][AE].Eff1.小ドレイン) {
          delete data.C1[cNameF][AE].Eff1.小ドレイン
        }






        Object.keys(data.C1[cNameF][AE].Eff1).map((EffName) => {
          data.C1[cNameF][AE].Eff1[EffName].forEach((EffEl) => {

            data.C1[cNameF][AE].Eff9.push({
              ...EffEl,
              EV: EffEl.V ? EffEl.E + EffEl.V : EffEl.E
            })
          })
        })
      })//バフ・デバフ整理




      data.list.AllyEnmy.forEach((AE) => {//Eff9(DstaGridBffのデータ)展開

        data.C1[cNameF][AE].StMlt = {}//ステータスのバフ枠
        data.C1[cNameF][AE].StMltStkMax = {}//ステータスのバフ枠でStkMaxがあるもの

        data.list.HTADSIM.forEach((S) => {
          data.C1[cNameF][AE].StMlt[S] = 0//ステータスのバフ乗算
          data.C1[cNameF][AE].StMltStkMax[S] = 0//ステータスのバフ乗算
          data.C1[cNameF][AE].St.Btl[S] = data.C1[cNameF][AE].St.Org[S]
        })




        data.C1[cNameF][AE].Eff9.forEach((EffEl) => {


          let V = EffEl.V ? (+EffEl.V) ?//数値なら(+をつけると数値として判断)
            EffEl.V : Number(EffEl.V.split('%')[0]) : ''
          let sign = EffEl.EV.includes('+') ? '+' : '-'
          if (sign === '-') V *= -1


          let E = EffEl.E
          if (isIncludes(data.list.Gr, EffEl.E)) {//効果名にグループが含まれているなら
            if (EffEl.E.includes(Gr)) {//自分のグループなら
              E = EffEl.E.split(Gr)[1]
            } else {//違うなら無効
              E = ''
            }
          }









          if (EffEl.EV.includes('消費TP-')) {

          } else if (isIncludes(data.list.HTADSIM, EffEl.EV)) {//Effにステータスが含まれていれば
            let sign = EffEl.EV.includes('+') ? '+' : '-'
            let S = EffEl.EV.split(sign)[0]//S=ステータス
            if (isIncludes(data.list.Gr, S) && S.includes(Gr)) {//Grが含まれていて、グループに合致すれば
              S = S.split(Gr)[1]
            }

            if (S === '移動力') {
              data.C1[cNameF][AE].St.Btl[S] += V
              data.C1[cNameF][AE].St.Btl[S] = Math.max(data.C1[cNameF][AE].St.Btl[S], 0)
            } else {
              if (EffEl.StkMax) {
                data.C1[cNameF][AE].StMltStkMax[S] += V//StkMaxがあるステータスバフは別枠にしておく
              } else {
                data.C1[cNameF][AE].StMlt[S] += V
              }
              // if (cNameF === '【戒律を遵守せし傲慢の使徒】ヴァネッサ(鏡界)' && AE === 'Ally') {
              //   console.log(EffEl.EV)
              //   console.log(EffEl.StkMax)
              // }


            }

            // } else if (EffEl.EV.includes('威力')) {
          } else if (E.includes('威力')) {
            // console.log()

            if (E.includes('威力・')) {
              E = E.split('威力・')[0] + '威力'//・効果などを削除
            }


            let ElmKndArr = []

            if (E.split('威力')[0] === '') {//威力の前に文字列が無ければ全
              ElmKndArr.push('全')

            } else if (isIncludes(data.C1[cNameF][AE].Skl, E.split('威力')[0])) {//名称がスキルなら
              ElmKndArr.push(E.split('威力')[0])

            } else if (E.includes('・')) {

              const Knd = data.list.Knd.find(el => E.includes(el))
              let EArr = E.split('威力+')[0].split('・')

              // if (cNameF === '【慈悲を司る大天使ウリエル】ヒューレー' && AE === 'Ally') {
              //   console.log(E)
              // }


              // console.log(E.split('威力+')[0].split('・'))
              EArr.forEach((EK) => {



                if (EK === '炎' || EK === '水' || EK === '氷' || EK === '風' || EK === '岩' || EK === '雷' || EK === '冥' || EK === '無') {//「属性」が付いていないなら付ける
                  ElmKndArr.push(EK + '属性' + Knd)
                } else {
                  ElmKndArr.push(EK)
                }

              })






            } else {//普通の威力

              ElmKndArr.push(E.split('威力')[0])



            }


            // let ElmKnd = EffEl.EV.split('威力')[0] === '' ? '全' : EffEl.EV.split('威力')[0]//威力の前に文字列が無ければ全

            // if (cNameF === '【聖夜の奇跡をあなたに】ナディラ') {
            //   console.log(AE)
            //   console.log(ElmKnd)
            //   console.log(sign)
            //   console.log(V)
            //   // console.log(data.C1[cNameF][AE])
            //   // console.log(isIncludes(data.C1[cNameF][AE].Skl, ElmKnd))
            // }

            // let ElmKndList = [ElmKnd]
            // if (
            //   !isIncludes(data.C1[cNameF][AE].Skl, ElmKnd) &&//名称がスキル名でない
            //   ElmKnd.includes('・')//かつ炎・氷・風属性など
            // ) {
            //   const Knd = data.list.Knd.find(el => ElmKnd.includes(el))
            //   // console.log(Knd)



            //   ElmKndList = ElmKnd.split('・').map((EK) => {

            //     if (isIncludes(data.list.Gr, EK)) {//Grが含まれているなら
            //       if (EK.includes(Gr)) {//Grが合っているなら
            //         return '全'
            //       }

            //     } else if (EK === '炎' || EK === '水' || EK === '氷' || EK === '風' || EK === '岩' || EK === '雷' || EK === '冥' || EK === '無') {//「属性」が付いていないなら付ける
            //       return EK + '属性' + Knd
            //     } else {
            //       return EK
            //     }
            //   })
            // } else {

            //   if (isIncludes(data.list.Gr, ElmKnd)) {//Grが含まれているなら
            //     ElmKndList = ElmKnd.split('・').map((EK) => {
            //       if (EK.includes(Gr)) {//グループ限定効果がマッチしているなら
            //         return EK.split(Gr)[1]
            //       }
            //     })
            //   }
            // }

            // ElmKndList.forEach((ElmKnd) => {
            //   if (!data.C1[cNameF][AE].威力[ElmKnd]) {
            //     data.C1[cNameF][AE].威力[ElmKnd] = V
            //   } else {
            //     data.C1[cNameF][AE].威力[ElmKnd] += V
            //   }
            // })

            ElmKndArr.forEach((ElmKnd) => {
              if (!data.C1[cNameF][AE].威力[ElmKnd]) {
                data.C1[cNameF][AE].威力[ElmKnd] = V
              } else {
                data.C1[cNameF][AE].威力[ElmKnd] += V
              }
            })



          } else if (EffEl.EV.includes('耐性')) {

            // console.log(EffEl.EV)
            // console.log(EffEl)
            let Elm = EffEl.EV.split('耐性')[0]


            let AE2 = EffEl.Tgt
            if (AE !== EffEl.Tgt) {
              AE2 = EA[EffEl.Tgt]
            }

            // console.log(Elm)
            // if (Elm === '魔法') console.log(AE2)

            if (isIncludes(data.list.Knd, Elm)) {
              let Knd = Elm
              if (isIncludes(data.list.Gr, Elm) && Elm.includes(Gr)) {
                Knd = Knd.split(Gr)[1]
              }

              // console.log(Knd + '耐性')
              // console.log(AE2)
              // console.log(EffEl.EV)
              // console.log(V)

              data.C1[cNameF][AE2].増減[Knd + '耐性'] += V
            } else {
              data.C1[cNameF][AE2].耐性[Elm] += V
            }


            // console.log(AE2)
            // console.log(EffEl.Tgt)




          } else if (EffEl.EV.includes('逆境') && data.ZHP === 0) {

            if (EffEl.EV.includes('小')) {
              data.C1[cNameF][AE].逆境[EffEl.EV] = 60
            } else if (EffEl.EV.includes('大')) {
              data.C1[cNameF][AE].逆境[EffEl.EV] = 220
            } else {
              data.C1[cNameF][AE].逆境[EffEl.EV] = 150
            }
            // if (EffEl.EV.includes('小')) {
            //   data.C1[cNameF][AE].逆境[EffEl.EV] = roundAdv(1.6 - 1 * ZHPRt[AE] / 100 + 0.4 * ZHPRt[AE] / 100 * ZHPRt[AE] / 100)
            // } else {
            //   data.C1[cNameF][AE].逆境[EffEl.EV] = roundAdv(2.5 - 2.5 * ZHPRt[AE] / 100 + 1 * ZHPRt[AE] / 100 * ZHPRt[AE] / 100)
            // }

          } else if (EffEl.EV.includes('盤石') && data.ZHP === 100) {

            if (EffEl.EV.includes('小')) {
              data.C1[cNameF][AE].盤石[EffEl.EV] = 25
            } else if (EffEl.EV.includes('大')) {
              data.C1[cNameF][AE].盤石[EffEl.EV] = 80
            } else {
              data.C1[cNameF][AE].盤石[EffEl.EV] = 49.14
            }


            // } else if (EffEl.EV.includes('クリティカル率+')) {
            data.C1[cNameF][AE].Crr += V
          } else if (EffEl.EV.includes('被ダメージ軽減+')) {
            data.C1[cNameF][AE].増減.被ダメージ軽減 += V
          } else if (EffEl.EV.includes('被ダメージ増加+')) {
            data.C1[cNameF][AE].増減.被ダメージ増加 += V

          } else if (EffEl.EV.includes('与ダメージ回数')) {
            // let SklIndex = data.C1[cNameF][AE].Skl.indexOf(EffEl.EV.split('与ダメージ回数')[0])

            let SklNam = EffEl.EV.split('与ダメージ回数')[0]
            if (!data.C1[cNameF][AE].与ダメージ回数[SklNam]) {
              data.C1[cNameF][AE].与ダメージ回数[SklNam] = 0
            }
            data.C1[cNameF][AE].与ダメージ回数[SklNam] += V
            // if (cNameF === '【一番いいのを頼むわ】ソフィア') {
            //   console.log(EffEl.EV)
            //   console.log(data.C1[cNameF][AE])
            //   console.log(V)
            // }
          } else if (EffEl.EV === '回避') {
            data.C1[cNameF][AE].回避 = true

          } else if (EffEl.EV.includes('チャージ+')) {
            let SklNam = EffEl.EV.split('チャージ')[0]
            if (!data.C1[cNameF][AE].チャージ[SklNam]) {
              data.C1[cNameF][AE].チャージ[SklNam] = 0
            }
            data.C1[cNameF][AE].チャージ[SklNam] += V


          } else if (EffEl.EV === '被ダメージ無効バリア') {
            data.C1[cNameF][AE].被ダメージ無効バリア = true
          }


        })
      })//Eff9展開

      data.list.AllyEnmy.forEach((AE) => {//チャージ
        Object.keys(data.C1[cNameF][AE].チャージ).forEach((SklNam) => {//チャージ
          // let SklNam = EffEl.EV.split('与ダメージ回数')[0]
          if (!data.C1[cNameF][AE].与ダメージ回数[SklNam]) {
            data.C1[cNameF][AE].与ダメージ回数[SklNam] = 0
          }
          data.C1[cNameF][AE].与ダメージ回数[SklNam] += Math.min(
            data.C1[cNameF][AE].チャージ[SklNam],
            data.Skl[SklNam].CgMax
          )

        })

      })//チャージ

      data.list.AllyEnmy.forEach((AE) => {//戦闘時ステータス
        data.list.HTADSIM.forEach((S) => {
          data.C1[cNameF][AE].St.Btl[S] *= (100 + data.C1[cNameF][AE].StMlt[S] + data.C1[cNameF][AE].StMltStkMax[S]) / 100
          data.C1[cNameF][AE].St.Btl[S] = round(data.C1[cNameF][AE].St.Btl[S])
        })
      })//戦闘時ステータス


      data.list.AllyEnmy.forEach((AE) => {//ダメージ計算

        data.C1[cNameF][AE].Skl3.forEach((SklEl, i) => {

          SklEl.Dmg = 0//補助のための0
          SklEl.Amp = 0


          let SklNam = SklEl.Nam
          let SA = SklEl.No === '特性' ? 'Ab' : 'Skl'
          let Elm = SklEl.Elm + '属性'
          let Knd = SklEl.Knd
          let Str = (SklEl.Str ? SklEl.Str : 0) + (SklEl.攻撃前Str ? SklEl.攻撃前Str : 0)
          let Num = SklEl.Num >= 2 ? SklEl.Num : 1

          if (data.C1[cNameF][AE].与ダメージ回数[SklNam]) {
            Num += data.C1[cNameF][AE].与ダメージ回数[SklNam]
          }

          let MM2 = 1
          if (AE === 'Ally' && data.Enmy[Enmy].Sz !== '1x1') {//1x1を渡すのはNG
            MM2 = MM(data[SA][SklNam].Ara, data.Enmy[Enmy].Sz)
          }

          let Mag = SklEl.Mag

          const Lv = data.C1[cNameF][AE].Lv
          const T = data.C1[cNameF][AE].St.Btl.TP
          let D = data.C1[cNameF][EA[AE]].St.Btl.防御力//敵

          const AI = {//攻撃前に上がる分
            攻撃力: data.C1[cNameF][AE].St.Btl.攻撃力,
            精神力: data.C1[cNameF][AE].St.Btl.精神力,
          }
          Object.keys(AI).forEach((S) => {
            if (SklEl[S]) {
              AI[S] = data.C1[cNameF][AE].St.Org[S] * (100 + data.C1[cNameF][AE].StMlt[S] + Math.min(data.C1[cNameF][AE].StMltStkMax[S] + SklEl[S], 45)) / 100
              AI[S] = round(AI[S])
            }
          })
          const A = AI.攻撃力
          const I = AI.精神力

          if (SklEl.攻撃前敵防御力) {
            // console.log(data.C1[cNameF][EA[AE]].St.Org.防御力)
            // console.log(data.C1[cNameF][EA[AE]].StMlt.防御力)
            // console.log(data.C1[cNameF][EA[AE]].StMltStkMax.防御力)
            // console.log(SklEl.攻撃前敵防御力)

            D = data.C1[cNameF][EA[AE]].St.Org.防御力 * (100 + data.C1[cNameF][EA[AE]].StMlt.防御力 + Math.min(data.C1[cNameF][EA[AE]].StMltStkMax.防御力 + SklEl.攻撃前敵防御力, 75)) / 100
            D = round(D)
            // console.log(D)
          }


          if (Mag) {//攻撃スキル(威力あり)なら

            // if (cNameF === '【一番いいのを頼むわ】ソフィア') {
            //   console.log(SklNam)
            //   console.log(SklEl.Num)
            //   console.log(Num)
            // }

            if (Knd === '物理') {
              SklEl.Dmg = Math.max(A / 2 - D / 4, 0) * Mag / 100
              SklEl.BasDmgStr = '(攻' + A + '/2-敵防' + D + '/4)*' + Mag / 100

            } else {//体技波動魔法

              if (Knd === '魔法') {
                if (SklEl.TPAll) {//絶対零度など
                  SklEl.Dmg = T * Mag
                  SklEl.BasDmgStr = '(消費TP' + T + '*' + Mag + ')'

                } else {//普通の魔法攻撃
                  SklEl.Dmg = I * Mag + SklEl.Add

                  if (SklEl.Cap) {//キャップが設定されているなら
                    SklEl.Dmg = Math.min(SklEl.Dmg, SklEl.Cap[1])
                    SklEl.BasDmgStr = '(min(精神' + I + ',' + SklEl.Cap[0] + ')'
                  } else {
                    SklEl.BasDmgStr = '(精神' + I
                  }
                  SklEl.BasDmgStr += '*' + Mag + '+' + SklEl.Add + ')'

                }

              } else {//体技波動なら
                SklEl.Dmg = Lv * Mag + SklEl.Add
                SklEl.BasDmgStr = '(Lv' + Lv + '*' + Mag + '+' + SklEl.Add + ')'

              }

              SklEl.Dmg *= (100 + Str) / 100//スキル強化
              if (Str) {
                SklEl.BasDmgStr += '*' + (100 + Str) / 100
              }

            }

            SklEl.Amp +=
              data.C1[cNameF][AE].威力.全 +
              data.C1[cNameF][AE].威力[Knd] +
              data.C1[cNameF][AE].威力[Elm] +
              data.C1[cNameF][AE].威力[Elm + Knd] +
              (SklEl.攻撃前威力 ? SklEl.攻撃前威力 : 0)
            if (data.C1[cNameF][AE].威力[SklNam]) {
              SklEl.Amp += data.C1[cNameF][AE].威力[SklNam]
            }
            SklEl.Amp = (100 + SklEl.Amp) / 100

            const ElmKnds = ['', Elm, Knd, Elm + Knd]

            const Advs = ElmKnds.map((EK) => {
              return (
                Math.max(
                  data.C1[cNameF][AE].逆境[EK + '逆境'],
                  SklEl[EK + '逆境'] ? 150 : 0,
                  data.C1[cNameF][AE].逆境[EK + '逆境(大)'],
                  SklEl[EK + '逆境(大)'] ? 220 : 0,
                  data.C1[cNameF][AE].逆境[EK + '逆境(小)'],
                  SklEl[EK + '逆境(小)'] ? 60 : 0,
                )
              )
            })
            const Bnjs = ElmKnds.map((EK) => {
              return (
                Math.max(
                  data.C1[cNameF][AE].盤石[EK + '盤石'],
                  SklEl[EK + '盤石'] ? 49.14 : 0,
                  data.C1[cNameF][AE].盤石[EK + '盤石(大)'],
                  SklEl[EK + '盤石(大)'] ? 80 : 0,
                  data.C1[cNameF][AE].盤石[EK + '盤石(小)'],
                  SklEl[EK + '盤石(小)'] ? 25 : 0,
                )
              )
            })


            // if (cNameF === '【雪をも溶かす熱き信念】アレッシア') {
            //   console.log(Ban)
            // }


            // SklEl.逆境盤石 = Math.round((100 +
            //   Math.max(data.C1[cNameF][AE].逆境.逆境, ((SklEl.逆境 && data.ZHP === 0) ? 150 : 0)) +
            //   Math.max(data.C1[cNameF][AE].逆境['逆境(大)'], ((SklEl['逆境(大)'] && data.ZHP === 0) ? 220 : 0)) +
            //   Math.max(data.C1[cNameF][AE].逆境['逆境(小)'], ((SklEl['逆境(小)'] && data.ZHP === 0) ? 60 : 0)) +
            //   Math.max(data.C1[cNameF][AE].逆境[Knd + '逆境'], ((SklEl[Knd + '逆境'] && data.ZHP === 0) ? 150 : 0)) +
            //   Math.max(data.C1[cNameF][AE].逆境[Knd + '逆境(大)'], ((SklEl[Knd + '逆境(大)'] && data.ZHP === 0) ? 220 : 0)) +
            //   Math.max(data.C1[cNameF][AE].逆境[Knd + '逆境(小)'], ((SklEl[Knd + '逆境(小)'] && data.ZHP === 0) ? 60 : 0)) +
            //   Math.max(data.C1[cNameF][AE].逆境[Elm + '逆境'], ((SklEl[Elm + '逆境'] && data.ZHP === 0) ? 150 : 0)) +
            //   Math.max(data.C1[cNameF][AE].逆境[Elm + '逆境(大)'], ((SklEl[Elm + '逆境(大)'] && data.ZHP === 0) ? 220 : 0)) +
            //   Math.max(data.C1[cNameF][AE].逆境[Elm + '逆境(小)'], ((SklEl[Elm + '逆境(小)'] && data.ZHP === 0) ? 60 : 0)) +
            //   Math.max(data.C1[cNameF][AE].逆境[Elm + Knd + '逆境'], ((SklEl[Elm + Knd + '逆境'] && data.ZHP === 0) ? 150 : 0)) +
            //   Math.max(data.C1[cNameF][AE].逆境[Elm + Knd + '逆境(大)'], ((SklEl[Elm + Knd + '逆境(大)'] && data.ZHP === 0) ? 220 : 0)) +
            //   Math.max(data.C1[cNameF][AE].逆境[Elm + Knd + '逆境(小)'], ((SklEl[Elm + Knd + '逆境(小)'] && data.ZHP === 0) ? 60 : 0)) +
            //   Math.max(data.C1[cNameF][AE].盤石.盤石, ((SklEl.盤石 && data.ZHP === 100) ? 49.14 : 0)) +
            //   Math.max(data.C1[cNameF][AE].盤石['盤石(大)'], ((SklEl['盤石(大)'] && data.ZHP === 100) ? 80 : 0)) +
            //   Math.max(data.C1[cNameF][AE].盤石['盤石(小)'], ((SklEl['盤石(小)'] && data.ZHP === 100) ? 25 : 0)) +
            //   Math.max(data.C1[cNameF][AE].盤石[Knd + '盤石'], ((SklEl[Knd + '盤石'] && data.ZHP === 100) ? 49.14 : 0)) +
            //   Math.max(data.C1[cNameF][AE].盤石[Knd + '盤石(大)'], ((SklEl[Knd + '盤石(大)'] && data.ZHP === 100) ? 80 : 0)) +
            //   Math.max(data.C1[cNameF][AE].盤石[Knd + '盤石(小)'], ((SklEl[Knd + '盤石(小)'] && data.ZHP === 100) ? 25 : 0)) +
            //   Math.max(data.C1[cNameF][AE].盤石[Elm + '盤石'], ((SklEl[Elm + '盤石'] && data.ZHP === 100) ? 49.14 : 0)) +
            //   Math.max(data.C1[cNameF][AE].盤石[Elm + '盤石(大)'], ((SklEl[Elm + '盤石(大)'] && data.ZHP === 100) ? 80 : 0)) +
            //   Math.max(data.C1[cNameF][AE].盤石[Elm + '盤石(小)'], ((SklEl[Elm + '盤石(小)'] && data.ZHP === 100) ? 25 : 0)) +
            //   Math.max(data.C1[cNameF][AE].盤石[Elm + Knd + '盤石'], ((SklEl[Elm + Knd + '盤石'] && data.ZHP === 100) ? 49.14 : 0)) +
            //   Math.max(data.C1[cNameF][AE].盤石[Elm + Knd + '盤石(大)'], ((SklEl[Elm + Knd + '盤石(大)'] && data.ZHP === 100) ? 80 : 0)) +
            //   Math.max(data.C1[cNameF][AE].盤石[Elm + Knd + '盤石(小)'], ((SklEl[Elm + Knd + '盤石(小)'] && data.ZHP === 100) ? 25 : 0))
            // ) * 100) / 10000

            if (data.ZHP === 100) {
              SklEl.逆境盤石 = Math.round((100 +
                Bnjs[0] +
                Bnjs[1] +
                Bnjs[2] +
                Bnjs[3]
              ) * 100) / 10000
            }else if(data.ZHP === 0){
              SklEl.逆境盤石 = Math.round((100 +
                Advs[0] +
                Advs[1] +
                Advs[2] +
                Advs[3]
              ) * 100) / 10000
            }


            // if (cNameF === '【新年最初のドキドキ】ライサ' && SklNam === '破裂の羽根つき') {
            //   console.log(SklNam)
            //   console.log(SklEl[Knd + '逆境(小)'])
            //   console.log(SklEl.逆境盤石)
            // }


            SklEl.耐性 = (100 - data.C1[cNameF][EA[AE]].耐性[Elm]) / 100

            SklEl.増減 = (100 +
              data.C1[cNameF][EA[AE]].増減.被ダメージ軽減 * -1 +
              data.C1[cNameF][EA[AE]].増減.被ダメージ増加 +
              data.C1[cNameF][EA[AE]].増減[Knd + '耐性'] * -1
            ) / 100


            SklEl.Dmg *= SklEl.Amp * SklEl.逆境盤石 * SklEl.耐性 * SklEl.増減
            SklEl.Dmg = round(SklEl.Dmg)



            SklEl.DmgTxt = SklEl.Dmg
            if (Num >= 2) SklEl.DmgTxt += '×' + Num + '回'
            if (MM2 >= 2) SklEl.DmgTxt += '×' + MM2 + 'マス'

            SklEl.Dmg *= Num * MM2

            if (SklEl.Dmg) {//UseState用。ダメージが変化したら更新
              TDmg += SklEl.Dmg
            }

            if (data.C1[cNameF][EA[AE]].回避 && (Knd === '物理' || Knd === '体技')) {
              SklEl.Dmg = 0
              SklEl.DmgTxt = '回避'
            }
            if (data.C1[cNameF][EA[AE]].被ダメージ無効バリア) {
              SklEl.Dmg = 0
              SklEl.DmgTxt = '被ダメ無効'
            }

          }//if Mag






        })//data.C1[cNameF][AE].Skl3.forEach
      })//data.list.AllyEnmy.forEachダメージ計算






    })//Object.keys(data.C1).forEachここまで

    hoge(TDmg)

  }

  console.log(data)







  // setUnit(TDmg)
  React.useEffect(() => { Calc() }, [])//第二引数は副作用が引き起こされるかどうかの依存関係= []は初回レンダリング後のみ実行

  return <Tabtop props={{ data: data, Calc: Calc, round: round }} />

}

