import * as React from 'react';
import Box from '@mui/material/Box';
import { DataGrid } from '@mui/x-data-grid';
import Tooltip from '@mui/material/Tooltip';
import InfoIcon from '@mui/icons-material/Info';


const columns = [
  {
    field: 'EV',
    headerName: '',
    width: 250,
    renderHeader: (params) => (
      <Tooltip title="行動開始時点でのバフです。また、ターン経過や延長には対応しておらず、付与されたときの効果ターンがそのまま表示されます。" placement="top-start" arrow >
        <InfoIcon color="disabled" />
      </Tooltip>
    ),
  },
  {
    field: 'JT',
    headerName: 'ターン',
    // headerAlign: 'center',
    align: 'center',
    width: 63,
    // sortable: false,//false:ソート不可
    disableColumnMenu: true,//メニュー不可
    description: '効果ターン',
  },
  {
    field: 'N',
    headerName: '付与元',
    width: 200,
  },
  // {
  //   field: 'Ara',
  //   headerName: '射程範囲',
  //   width: 150,
  //   align: 'center',
  // },

  // {
  //   field: 'DmgTxt',
  //   headerName: 'ダメージ',
  //   width: 150,
  //   align: 'center',
  // },
];


export default function DataGridBff(props) {
  const { data, cNameF, AE } = props.props

  const rows = data.C1[cNameF][AE].Eff9.map((EffEl, i) => {
    return ({
      ...EffEl,
      id: i,
    })
  })

  return (
    <Box sx={{ height: 300, width: '100%' }}>
      <DataGrid
        rows={rows}
        columns={columns}
        experimentalFeatures={{ newEditingApi: true }}
        density='compact'//行の幅
        // autoHeight//行に合わせた高さ
        hideFooter//フッター非表示
        hideFooterSelectedRowCount//フッターに表示されている選択中の行数を非表示
        hideFooterPagination//フッターに表示されているページ当たりの行数、ページングを非表示にする。(無料版のみ）
        getRowHeight={() => 'auto'}
      />
    </Box>
  );
}